<template>
  <div class="modal" @click="$emit('closeModal')">
    <div class="modal__window" @click.stop>
      <h3 class="modal__title">
        {{ edit ? words.titlewindowedit[lang] : words.titlewindow[lang] }}
      </h3>
      <div class="modal__inputs">
        <label>
          <span>Title</span>
          <input type="text" placeholder="Title" v-model="title" />
        </label>
        <label>
          <span>Content</span>
          <input type="text" placeholder="Content" v-model="text" />
        </label>
      </div>
      <div class="modal__buttons">
        <button class="modal__btn del" @click="$emit('closeModal')">
         {{words.closebtn[lang]}}
        </button>
        <button v-if="edit" class="modal__btn edit" @click="changedNote">
          {{words.editbtn[lang]}}
        </button>
        <button v-else class="modal__btn edit" @click="addNote">
          {{words.addbtn[lang]}}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      title: null,
      text: null,
    };
  },
  methods: {
    addNote() {
      if (this.title != null && this.text != null) {
        let newNote = {
          title: this.title,
          text: this.text,
        };
        this.$emit("addNote", newNote);
      }
    },
    changedNote() {
      if (this.title != null && this.text != null) {
        let changedNote = {
          id:this.changeNote.id,
          title: this.title,
          text: this.text,
          date:new Date().toLocaleDateString()
        };
        this.$emit("changedNote", changedNote);
      }
    },
  },
  props: {
    edit: Boolean,
    changeNote: Object,
    lang:String
  },
  inject:['words'],
  mounted() {
    if (this.edit) {
      this.title = this.changeNote.title;
      this.text = this.changeNote.text;
    }
  },
};
</script>

